	module.exports = {"data":{"site":{"siteMetadata":{"title":"My Code bad","author":"Guillermo Paredes Torrez"}},"markdownRemark":{"id":"/home/gd/Development/MyCodeBad/blog/src/pages/react-parte-0/index.md absPath of file >>> MarkdownRemark","html":"<h3>Introducción</h3>\n<p>En esta serie, crearemos una pequeña aplicación de Tareas, que tendra una Api Rest el marco a utilizar se decidira más adelante; pero estoy en estas 2 opciones: Nodejs o Laravel.</p>\n<h3>Requisitos</h3>\n<p>Antes de comenzar el proyecto, necesitamos tener instalado algunos requerimientos, que describire a continuación:</p>\n<ul>\n<li>Conocimientos de Javascript, css3, html5.</li>\n<li>Tener instalado <a href=\"https://nodejs.org/es/\">Node.js</a></li>\n<li>Tener instalado el cliente de react <a href=\"https://github.com/facebook/create-react-app\">create-react-app</a></li>\n</ul>\n<p>Para tener bases de javascript, les puedo recomendar este curso de <a href=\"https://www.youtube.com/playlist?list=PLhSj3UTs2_yVC0iaCGf16glrrfXuiSd0G\">@FalconMaster</a></p>\n<h3>Mockup</h3>\n<p>La maquetación que haremos para la aplicación es algo sencilla, pero la diseñe en esta app\n<a href=\"https://mockflow.com/\">mockflow</a>.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blog/static/react-app-mockup-7ccccae49fd284e35a57bb003180af54-94ad1.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 80.31341821743389%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"react tareas app\"\n        title=\"\"\n        src=\"/blog/static/react-app-mockup-7ccccae49fd284e35a57bb003180af54-f8fb9.jpg\"\n        srcset=\"/blog/static/react-app-mockup-7ccccae49fd284e35a57bb003180af54-e8976.jpg 148w,\n/blog/static/react-app-mockup-7ccccae49fd284e35a57bb003180af54-63df2.jpg 295w,\n/blog/static/react-app-mockup-7ccccae49fd284e35a57bb003180af54-f8fb9.jpg 590w,\n/blog/static/react-app-mockup-7ccccae49fd284e35a57bb003180af54-85e3d.jpg 885w,\n/blog/static/react-app-mockup-7ccccae49fd284e35a57bb003180af54-94ad1.jpg 1021w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>En los siguientes posts, estaremos comenzando con la maquetación.</p>\n<p>Nos vemos. My Code Bad!</p>","frontmatter":{"title":"React.js - Parte 0","date":"August 27, 2018"}}},"pathContext":{"slug":"/react-parte-0/","previous":{"fields":{"slug":"/ReactBolivia/"},"frontmatter":{"title":"Comunidad React Bolivia"}},"next":null}}

